@import '../../App.scss';

.offer{
    background-image: url(https://cdn.shopify.com/s/files/1/0304/5739/7386/files/bg_bannerv5.jpg?v=1613680094);
    display: flex;  
}
.offer-content{
    display: flex;
    flex-wrap: wrap;
    padding: 3rem 3rem;
}
.video{
    flex: 0 0 100%;  
    order: 2; 
}
.video img{
   width: 100%;
   height: 100%;
   object-fit: cover;
   margin-bottom: 3rem;
}
.txt{
    flex: 1 1 100%; 
    order: 1;  
    margin-left: 0;
    display: flex;
    align-items: center;
    align-content: center;
    direction: ltr;  
    text-align: left;
    margin-bottom: 2rem;
}
.txt-content{
   vertical-align: middle;
}
.shop-btn
{
    padding: 1rem 2rem;
    background-color:$cream;
    color: #fff;
    display: inline-block;
    margin-top: 2rem;
}

.offer-h3{
    color: #fff;
    font-weight: 300;
    font-size: 1.3rem;
    margin: 0
}
.txt-content h2{
    margin-top: 0.8rem;
    color: #fff;
    font-weight: 700;
    font-size: 3rem;
    margin-block-end: 0;
    margin-block-start: 0;
}
.txt-content p{
    margin-top: 0.5rem;
    font-size: 1rem;
    color: #fff;
}


@media screen and (min-width : 680px) {       
    .video{
        flex: 1 1 45%;  
           order: 1;
    }
    .video img{
        width: 100%;
     }
    .txt{
        flex: 0 0 45%;
        order: 2;
        margin-left: 3rem;
    }
}
