@import '../../App.scss';

.features{
    display: flex;
    flex-wrap: wrap;
    padding: 3rem 3rem;
}
.feature{
    flex: 1 1 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; 
    margin-top: 1rem;
}

.feature:hover .feature-icon{
    color: $cream;
    border: solid 1px $cream;
}
.feature-icon{
    font-size: 2rem;
    color: $lightGray;
    margin-right: 1.5rem;
    border: solid 1px $lighterGray;
    border-radius: 50%;
    padding: 0.7rem;
    margin-bottom: 1rem;
    transition: all 1s;
}

.content{
    text-align: center;
}
span{
    font-weight: 500;
    color: $lightGray;
    font-size: 1rem;
    letter-spacing: 2px;
    line-height: 1.2;
}
p{
    font-weight: 100;
    color: $lighterGray;
    font-size: 0.9rem;
    line-height: 1.2;
}


@media screen and (min-width : 820px) {
    .feature{
        flex-direction: row;
        align-items: flex-start;
    }
    .content{
        text-align: left;
    }

}

@media screen and (min-width : 1000px) {
 .feature{
        flex: 1 1 33%;
 }
}

@media screen and (min-width : 1300px) {
    .right-border{
       border-right: solid $lightestGray 1px;  
   }
   }